import { Component, OnInit, HostListener, ViewChild } from '@angular/core';
import { ActivatedRoute } from '@angular/router';

import { MatDialog } from '@angular/material/dialog';

import { AccountsService } from 'src/app/api/controllers/Accounts';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';

import { jqxGridComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxgrid';
import { jqxPopoverComponent } from 'src/app/shared/jqwidgets-scripts/jqwidgets-ts/angular_jqxpopover';

@Component({
  selector: 'app-income-all-credits',
  templateUrl: './income-all-credits.component.html',
  styleUrls: ['./income-all-credits.component.scss']
})
export class IncomeAllCreditsComponent implements OnInit {

  constructor(
    public dialog: MatDialog,
    private route: ActivatedRoute,
    private accountsService:AccountsService,
    private cookieService:CookieService
  ) { }

  isCreditNoteLoaded:boolean = false;

  creditNoteData:string = "";
  creditNoteDataList:any;

  totalItems:any;

  columnData:any ;
  gridSourceData:any;
  popOverSelector:any;
  popOverPosition:any;

  credit:any;
  isEditCredit:boolean = false;

  isGeneralCredit:boolean = true;

  @ViewChild('datagrid', { static: false }) datagrid: jqxGridComponent;
  @ViewChild('reversePopOver', { static: false }) reversePopOver: jqxPopoverComponent;

  getPrintParams(event){
    this.datagrid.exportdata(event, 'CreditnoteData');
  }

  onGlSearchFilter() {

    if(this.creditNoteData != ""){

      let filtergroup = new jqx.filter();
      let filter_or_operator = 1;
      let filtervalue = this.creditNoteData;
      let filtercondition = 'contains';
      let filterData = filtergroup.createfilter('stringfilter', filtervalue, filtercondition);
      filtergroup.operator = 'or';
      filtergroup.addfilter(filter_or_operator, filterData);

      this.datagrid.showfiltercolumnbackground(false);

      this.columnData.forEach(item => {
        if(item.datafield != 'Actions') {
          this.datagrid.addfilter(item.datafield, filtergroup, true);
        }
      })

      this.datagrid.applyfilters();
      
    }
    else {
      this.datagrid.clearfilters();
    }

  }

  @HostListener('window:onEditCreditNote', ['$event.detail'])
  onEditCreditNote(detail) {

    this.isEditCredit = true;

    let dataRecord = this.datagrid.getrowdata(detail.rowId)
    let creditNoteId = dataRecord.creditNoteId;

    this.credit = dataRecord;

    this.popOverSelector = '#'+creditNoteId;
    this.popOverPosition = 'left'

    setTimeout(() => {
      this.reversePopOver.createComponent();
      this.reversePopOver.open();
    }, 300);

  }

  @HostListener('window:onUpdateInvoice', ['$event.detail'])
  onUpdateInvoice(detail) {

    var invoice = this.datagrid.getrowdata(detail.rowId);

  }

  getCreditParams(event) {
    this.getCreditNotesData();
  }

  addCreditNote() {
    this.isEditCredit = false;
  }

  getCreditNotesData() {

    this.isCreditNoteLoaded = false;

    let params = {
      apartmentId: parseInt(this.cookieService.get('apartmentId'))
    }

  	this.accountsService.getCreditNotesByApartmentId(params).subscribe((res:any) => {

      var creditNoteDataList = res;

      this.gridSourceData = {
        localdata: creditNoteDataList,
        datatype: "array"
      }
      
      this.creditNoteDataList = new jqx.dataAdapter(this.gridSourceData);
      this.totalItems = creditNoteDataList.length;

      this.isCreditNoteLoaded = true;
      
  	}, error => {
    	console.log(error);
    });

  }

  ngOnInit(): void {

    this.credit = {};

    var cellsrenderer = (row, column, value) => {
      return '<div class="jqx-custom-inner-cell">' + value + '</div>';
    }

    var columnrenderer = (value) => {
      return '<div style="padding: 14px">' + value + '</div>';
    }


    this.columnData = [{
      text: 'Creditnote Id',
      datafield: 'creditNoteId',
      width: 120,
      pinned: true,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    },
    {
      text: 'Amount',
      datafield: 'amount',
      width: 150,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    },
    {
      text: 'Invoice Id',
      datafield: 'invoiceId',
      width: 120,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    },
    {
      text: 'Collection Id',
      datafield: 'collectionId',
      width: 120,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    },
    {
      text: 'Comments',
      datafield: 'comment',
      minwidth: 180,
      cellsrenderer: cellsrenderer,
      renderer: columnrenderer
    },
    {
      text: 'Sent Email',
      datafield: 'isEmailSent',
      minwidth: 120,
      cellsrenderer: (row: number, column: string, value: boolean): string => {

      var chkedProperty;
      if(value){
        chkedProperty = 'checked="true"';
      }
      else {
        chkedProperty = '';
      }

        return '<div class="jqx-custom-inner-cell">'
                  + '<label class="switch float-left">'
                    + '<input type="checkbox" id="invoiceEmail" name="invoiceEmail"'+ chkedProperty +'>'
                    + '<div class="slider round" onClick="updateInvoiceEvent('+ row + ', \'email\')"></div>'
                  + '</label>'
            + '</div>'
      },
      renderer: columnrenderer
    },
    {
      text: 'Sent SMS',
      datafield: 'isSmssent',
      minwidth: 120,
      cellsrenderer: (row: number, column: string, value: boolean): string => {

      var chkedProperty;
      if(value){
        chkedProperty = 'checked="true"';
      }
      else {
        chkedProperty = '';
      }

        return '<div class="jqx-custom-inner-cell">'
                  + '<label class="switch float-left">'
                    + '<input type="checkbox" id="invoiceSms" name="invoiceSms"'+ chkedProperty +'>'
                    + '<div class="slider round" onClick="updateInvoiceEvent('+ row + ', \'sms\')"></div>'
                  + '</label>'
            + '</div>'
      },
      renderer: columnrenderer
    },
    {
      text: 'Actions',
      cellsalign: 'center',
      align: 'center',
      width: 120,
      cellsrenderer: (row: number): string => {

        let elemId = this.creditNoteDataList.loadedData[row].creditNoteId;

        return '<div class="simple-actions"> <a href="javascript:void(0)" class="mr-2" id="' + elemId + '" onClick="editCreditNoteEvent(' + row + ')" ><i class="icon fa fa-pencil edit" aria-hidden="true"></i></a></div>'
      },
      renderer: columnrenderer
    }]

    this.getCreditNotesData();

  }

}

let editCreditNoteEvent = row => {
  let event = new CustomEvent('onEditCreditNote', {
    detail: {
        rowId: row
    }
  })
  window.dispatchEvent(event);
}
(window as any).editCreditNoteEvent = editCreditNoteEvent;

let updateInvoiceEvent = (row, type) => {
  var event = new CustomEvent('onUpdateInvoice', {
    detail: {
        rowId: row,
        column: type
    }
  })
  window.dispatchEvent(event);
}
(window as any).updateInvoiceEvent = updateInvoiceEvent;